@media (max-width: 767px) {
  .product-item {
    &__content {
      padding: 10px 20px 10px 5%;
    }
  }
}
@media (max-width: 500px) {
  .product-item {
    flex-direction: column;
    align-items: center;
    &__cart-input {
      max-width: none;
    }
    &__cart {
      max-width: 100%;
    }
  }
}
